/* Product Panel Component styles */
.product-panel.cover {
  position: relative;
  margin: 0.8rem 2rem 0 0;
  width: 350px;
  height: 400px;
  background: #f6fdf5;
  border-radius: 0.2rem;
  -webkit-transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: box-shadow 0.3s, transform 0.3s;
  transition: box-shadow 0.3s, transform 0.3s, -webkit-transform 0.3s;
  overflow: hidden;
  box-shadow: 0 0 0 2px #787878, 0 0 12px 2.25px #777; }
  .product-panel.cover:empty {
    height: 0;
    margin-bottom: 0;
    margin-top: 0;
    box-shadow: none; }
  .product-panel.cover:hover {
    cursor: pointer;
    box-shadow: 0 0 0 2px #787878, inset -0.1rem 0 0 0 rgba(0, 0, 0, 0.05), inset 0 -0.1rem 0 0 rgba(0, 0, 0, 0.08), 0 0.4rem 2rem rgba(0, 0, 0, 0.05);
    -webkit-transform: translateY(-0.2rem);
            transform: translateY(-0.2rem); }
    .product-panel.cover:hover .cover-text-holder {
      height: 50%; }
  .product-panel.cover img {
    vertical-align: middle; }
  .product-panel.cover .cover-holder {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 1rem;
    justify-content: space-evenly;
    -webkit-transition: opacity 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
    transition: opacity 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out, opacity 0.1s ease-in-out;
    transition: transform 0.1s ease-in-out, opacity 0.1s ease-in-out, -webkit-transform 0.1s ease-in-out;
    top: 0px;
    position: absolute; }
  .product-panel.cover .cover-image {
    height: 100%;
    border: 1px solid #fff; }
    .product-panel.cover .cover-image .cover-image-holder {
      display: flex;
      justify-content: center;
      align-items: center;
      align-self: flex-end;
      height: 100%;
      border-radius: .3rem .3rem 0 0;
      overflow: hidden;
      background-image: url("https://firebasestorage.googleapis.com/v0/b/farmersaleplatfo-1561457656175.appspot.com/o/images%2Fanimal_rip_021563445560264.jpeg?alt=media&token=03513c4a-71a8-451a-b11c-fa7d37ecdad0");
      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat; }
  .product-panel.cover .cover-text-holder {
    text-align: center;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    height: 20%;
    position: absolute;
    bottom: 1px;
    left: 0;
    right: 0;
    margin: auto;
    width: calc(100% - 2px);
    border-radius: 0 0 .3rem .3rem;
    -webkit-transition: height ease-in-out 0.2s;
    transition: height ease-in-out 0.2s; }
    .product-panel.cover .cover-text-holder .cover-text-holder-title {
      font-size: 1.75em; }
    .product-panel.cover .cover-text-holder .cover-text-holder-info {
      font-size: 0.85em;
      color: lightgray;
      position: relative;
      text-align: right;
      width: calc(100% - 2px);
      display: flex;
      margin: 0 1px;
      justify-content: space-between; }
      .product-panel.cover .cover-text-holder .cover-text-holder-info span {
        display: inline-block;
        padding: 0 16px 0 6px;
        text-align: left;
        min-width: 50%;
        font-size: 20px;
        line-height: 37px;
        height: 30px;
        color: #fff;
        background-color: rgba(40, 167, 69, 0.1) !important; }
      .product-panel.cover .cover-text-holder .cover-text-holder-info span:last-child {
        text-align: right;
        padding: 0 6px 0 16px;
        background-color: rgba(23, 162, 184, 0.1) !important; }
    .product-panel.cover .cover-text-holder .cover-text-holder-line {
      border: 1px solid rgba(52, 59, 64, 0.85);
      margin: 0 1px; }

